import React from "react";
import "../experiencias/experiencias2.css"
import {react, node, mysql} from "../../midia/icones/fotos-icon"

function Experiencias() {
    return (
    <>
    
    <div className="main-container-experience">
        <div className="span-experiencias top">Minhas experincias</div>
        <div className="experience-content">
            <div className="L">
                <div className="experiencias">
                    <img className="img-experiencias" src={react} alt="" /> 
                    <div className="paragrafo-experiencias">
                        <h3>FRONT-END</h3>
                        Experincia em React, HTML, CSS, JavaScript e TypeScript, criando interfaces dinmicas e responsivas.<br/>
                        Desenvolvo componentes reutilizveis, gerencio estados e utilizo hooks para manipulao eficiente no React. <br/>
                        Estruturo pginas semnticas e aplico CSS Grid, Flexbox e media queries para compatibilidade. <br/>
                        Uso TypeScript para segurana e escalabilidade do cdigo.

                    </div>
                </div>
            </div>
            <div className="R">
                <div className="experiencias">     
                    <img className="img-experiencias-mobile" src={node} alt="" />          
                    <div className="paragrafo-experiencias">
                        <h3>BACK-END</h3>
                        Experincia em Node.js, desenvolvendo aplicaes server-side eficientes. 
                        Utilizo Express.js para estruturar rotas, gerenciar middleware e autenticao. <br/>
                        Trabalho na integrao entre backend e frontend, garantindo comunicao fluida via APIs RESTful (fetch e axios) e manipulao otimizada de dados.
                    </div>
                    <img className="img-experiencias mobile" src={node} alt="" />
                </div>
            </div>
            <div className="L">
                <div className="experiencias">
                    <img className="img-experiencias" src={mysql} alt=""/> 
                    <div className="paragrafo-experiencias">
                        <h3>BANCO DE DADOS</h3>
                        Experincia em MariaDB e MySQL, utilizando-os para gerenciamento eficiente de dados em aplicaes backend.<br/>
                        Trabalho com modelagem de banco, criao de tabelas e consultas SQL otimizadas para garantir desempenho e escalabilidade.
                    </div>
                </div>
            </div>
        </div>
        <div className="span-experiencias">Conhea um pouco mais dos projetos acessando meu GitHub 
            <a href="https://github.com/Gubz-js" target="_blank" rel="noreferrer noopener">
            <i className="fa-brands fa-github" id="git"></i>
            </a>
        </div>
    </div>
    </>
    )
}

export default Experiencias